.jumbotron{
  color: white;
  text-decoration: none;
  background-image: url(https://images.unsplash.com/photo-1561758033-d89a9ad46330?ixid=MXwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHw%3D&ixlib=rb-1.2.1&auto=format&fit=crop&w=1500&q=80);
  background-size: cover;
	background-position: center;
}

.menu-title{
  display: flex;
  justify-content: center;
}


.carousel-control-next-icon,
.carousel-control-prev-icon {
  filter: invert(1);
}

.menu-carousel{
  display: flex;
  justify-content: center;
  margin-bottom: 20px;
}

.footer{
  background-color: black;
  color: white;
  padding: 10px;
  text-align: center;

}

.footer a{
   color: white;
   display: flex;
   justify-content: center;
   margin: 10px 0;
   padding: 0;
}

.footer .container{
  width: 50%;
}

.social-media{
  display: flex;
  flex-direction: row;
}

.navigation{
  background-color: black;
}

#cart{
  color: white;
}

#basic-nav-dropdown{
  color: white;
}

.margin {
  margin-top: 20px;
  margin-bottom: 20px;
  padding: 0;
}

#login-form {
  width: 25%;
  border-radius: 10px;
  
  box-shadow: 3px 3px 5px 6px #ccc;
  padding: 10px;
}